/**
 * API Service Layer for Hate Speech Detection App
 * 
 * This module handles all communication with the backend API.
 * It provides a clean interface for our React components to use.
 */

import { AnalysisRequest, AnalysisResponse, SimpleAnalysisResponse, FlaggedWord } from '@/types';

// Configuration for API endpoints
const API_CONFIG = {
  baseUrl: process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000',
  endpoints: {
    analyze: '/analyze',
    analyzeSimple: '/analyze-simple',
    health: '/'
  },
  timeout: 30000, // 30 seconds timeout
} as const;

/**
 * Custom error class for API-related errors
 * This helps us handle different types of errors appropriately
 */
export class ApiError extends Error {
  constructor(
    message: string,
    public status?: number,
    public code?: string
  ) {
    super(message);
    this.name = 'ApiError';
  }
}

/**
 * Generic API request function with error handling
 * This is a utility function that handles common API patterns
 */
async function apiRequest<T>(
  endpoint: string,
  options: RequestInit = {}
): Promise<T> {
  const url = `${API_CONFIG.baseUrl}${endpoint}`;
  
  // Set default headers
  const defaultHeaders = {
    'Content-Type': 'application/json',
    'Accept': 'application/json',
  };

  try {
    const response = await fetch(url, {
      ...options,
      headers: {
        ...defaultHeaders,
        ...options.headers,
      },
    });

    // Check if response is ok
    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}));
      throw new ApiError(
        errorData.detail || errorData.message || `HTTP ${response.status}: ${response.statusText}`,
        response.status,
        errorData.code
      );
    }

    return await response.json();
  } catch (error) {
    // Handle network errors
    if (error instanceof ApiError) {
      throw error;
    }
    
    throw new ApiError(
      error instanceof Error ? error.message : 'Network error occurred',
      0,
      'NETWORK_ERROR'
    );
  }
}

/**
 * Analyze text for hate speech using the full analysis endpoint
 * 
 * @param request - The analysis request containing text and options
 * @returns Promise resolving to analysis results
 */
export async function analyzeText(
  request: AnalysisRequest
): Promise<AnalysisResponse> {
  try {
    const response = await apiRequest<AnalysisResponse>(API_CONFIG.endpoints.analyze, {
      method: 'POST',
      body: JSON.stringify({
        text: request.text,
        confidence_threshold: request.confidence_threshold || 0.6
      }),
    });

    return response;
  } catch (error) {
    console.error('Error analyzing text:', error);
    
    // Fallback to mock data if API is not available
    if (error instanceof ApiError && error.code === 'NETWORK_ERROR') {
      console.warn('API not available, using mock data');
      return createMockAnalysisResponse(request.text);
    }
    
    throw error;
  }
}

/**
 * Analyze text using the simple analysis endpoint
 * 
 * @param request - The analysis request containing text and options
 * @returns Promise resolving to analysis results
 */
export async function analyzeTextSimple(
  request: AnalysisRequest
): Promise<SimpleAnalysisResponse> {
  return apiRequest<SimpleAnalysisResponse>(API_CONFIG.endpoints.analyzeSimple, {
    method: 'POST',
    body: JSON.stringify({
      text: request.text,
      confidence_threshold: request.confidence_threshold || 0.6
    }),
  });
}

/**
 * Check if the API is healthy and responding
 * Useful for showing connection status to users
 */
export async function checkApiHealth(): Promise<{ message: string }> {
  return apiRequest<{ message: string }>(
    API_CONFIG.endpoints.health,
    { method: 'GET' }
  );
}

/**
 * Utility function to validate text input
 * Checks for common issues before sending to API
 */
export function validateTextInput(text: string): {
  isValid: boolean;
  errors: string[];
} {
  const errors: string[] = [];
  
  if (!text.trim()) {
    errors.push('Text cannot be empty');
  }
  
  if (text.length > 10000) {
    errors.push('Text is too long (maximum 10,000 characters)');
  }
  
  if (text.length < 5) {
    errors.push('Text is too short (minimum 5 characters)');
  }
  
  return {
    isValid: errors.length === 0,
    errors
  };
}

/**
 * Convert hate speech analysis to flagged words format for UI compatibility
 * 
 * @param analysisResponse - The analysis response containing hate speech clauses
 * @returns Array of flagged words
 */
export function convertToFlaggedWords(
  analysisResponse: AnalysisResponse
): FlaggedWord[] {
  const flaggedWords: FlaggedWord[] = [];
  
  analysisResponse.hate_speech_clauses.forEach(clause => {
    if (clause.is_hate_speech) {
      const originalText = analysisResponse.original_text;
      const clauseStartIndex = originalText.indexOf(clause.clause);
      
      if (clauseStartIndex !== -1) {
        // Extract significant words from rationale tokens
        const significantWords = clause.rationale_tokens
          .filter(token => token.is_rationale && token.confidence > 0.1)
          .map(token => token.token.replace('##', ''));
        
        // Create flagged words for each significant token
        significantWords.forEach(word => {
          const wordIndex = originalText.indexOf(word, clauseStartIndex);
          if (wordIndex !== -1) {
            flaggedWords.push({
              word,
              startIndex: wordIndex,
              endIndex: wordIndex + word.length,
              category: 'hate_speech',
              confidence: clause.confidence,
              suggestions: [
                { word: '[content removed]', confidence: 0.9, reason: 'Remove harmful content' },
                { word: '[inappropriate]', confidence: 0.8, reason: 'Mark as inappropriate' }
              ],
              explanation: clause.justification
            });
          }
        });
      }
    }
  });
  
  return flaggedWords;
}

/**
 * Create a mock analysis response for development/testing
 * This is useful when the backend isn't available
 */
function createMockAnalysisResponse(text: string): AnalysisResponse {
  return {
    original_text: text,
    total_clauses: 1,
    hate_speech_clauses: [],
    summary: {
      total_hate_speech_clauses: 0,
      highest_confidence: 0,
      average_confidence: 0
    }
  };
}

/**
 * Legacy function for backwards compatibility
 */
export function createMockAnalysisResponseLegacy(text: string): {
  originalText: string;
  flaggedWords: FlaggedWord[];
  analysisId: string;
  timestamp: string;
  confidence: number;
} {
  return {
    originalText: text,
    flaggedWords: [],
    analysisId: `mock-${Date.now()}`,
    timestamp: new Date().toISOString(),
    confidence: 0.85
  };
} 